import {Injectable} from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {Product} from './product';

@Injectable()
export class ProduitManagerService {

  constructor(public http: HttpClient) {
  }

  public getAllProduit(): Observable<Product[]> {
    return this.http.get('http://localhost:59583/api/produit');
  }

  public createProduct(product: Product): Observable<Product> {
    return this.http
      .post('http://localhost:59583/api/produit', product.getCleanDataForSending());

  }

  public deleteProduct(id: number): Observable<any> {
    return this.http.delete<string>('http://localhost:59583/api/produit', {
      params: new HttpParams().set('id', id + '')
    });
  }

  public updateProduct(prod: Product): Observable<any> {
    console.log(prod.id);
    return this.http.put('http://localhost:59583/api/produit', prod.getCleanDataForSending());
  }

  public getProduit(id: number): Observable<Product[]> {
    return this.http.get('http://localhost:59583/api/produit', {
      params: new HttpParams().set('id', id + '')
    });
  }
}





