import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {Product} from "../product";
import {OrderProduct} from "../order-product";
import {ProduitManagerService} from "../produit-manager.service";
import {Utilisateur} from "../utilisateur";
import {UtilisateurManagerService} from "../utilisateur-manager.service";

@Component({
  selector: 'app-list-utilisateur',
  templateUrl: './list-utilisateur.component.html',
  styleUrls: ['./list-utilisateur.component.css']
})
export class ListUtilisateurComponent implements OnInit {

  public tmpUser: Utilisateur = new Utilisateur();
  public listUser: Utilisateur[] = [];
  public listUserAjout: Utilisateur[] = [];
  public typeFilterUSer= 0;

  //Logan -- A quoi ça sert ?????
  @Output() public utilisateursChange: EventEmitter<Utilisateur[]> = new EventEmitter();

  constructor(public userService: UtilisateurManagerService) {
  }

  ngOnInit() {
    localStorage.getItem("token");
    this.userService.getAllUtilisateur().subscribe(listUser => {
      this.listUser = Utilisateur.fromJSONs(listUser);
      this.emitUtilisateurs();
    });
  }

  public returnUtilisateurList() {
    return this.listUser;
  }

  public createUser() {
    this.listUser.push(this.tmpUser);
    this.userService
      .createUtilisateur(this.tmpUser)
      .subscribe(utilisateur => {
        this.tmpUser.id = Utilisateur["Id_User"];
        this.emitUtilisateurs();
        this.emitUtilisateurAdd();
        this.tmpUser = new Utilisateur;
      });

  }

  public emitUtilisateurs() {
    this.utilisateursChange.next(this.listUser);
  }

  public deleteUtilisateur(index: number) {
    const DELETE_USER = () => {
      this.listUser.splice(index, 1);
      this.emitUtilisateurs();
    }
    const DISPLAY_ERROR = (error) => console.error(error);
    this.userService
      .deleteUtilisateur(this.listUser[index].id)
      .subscribe(DELETE_USER, DISPLAY_ERROR);
  }

  public updateUtilisateur(user: Utilisateur) {
    this.userService.updateUtilisateur(user).subscribe();
  }

  public emitUtilisateurAdd() {
    this.utilisateursChange.next(this.listUser);
  }
}



