export class Utilisateur {
  private _id: number;
  private _mdp: string;
  private _compte: string;
  private _sexe: string;
  private _login: string;
  private _dateNais: string;
  private _rue: string;
  private _num: string;
  private _codePostal: number;
  private _ville: string;
  private _pays: string;
  private _nom: string;
  private _status: boolean;


  constructor(nom: string = '', mdp: string = '', compte: string = '', sexe: string = '', login: string = '', dateNais: string = '', rue: string = '',
              num: string = '', codePostal: number = 0, ville: string = '', pays: string = '', status: boolean = false) {
    this._nom = nom;
    this._mdp = mdp;
    this._compte = compte;
    this._sexe = sexe;
    this._login = login;
    this._dateNais = dateNais;
    this._rue = rue;
    this._num = num;
    this._codePostal = codePostal;
    this._ville = ville;
    this._pays = pays;
    this._status = status;
  }

  public getCleanDataForSending(): any {
    let jsonToSend = {
      "Nom_User": this._nom,
      "Mdp_User": this._mdp,
      "Sexe_User": this._sexe,
      "DateNais_User": this._dateNais,
      "NumCompte_User": this._compte,
      "Login_User": this._login,
      "Rue_User": this._rue,
      "Num_User": this._num,
      "CodePostal_User": this._codePostal,
      "Ville_User": this._ville,
      "Pays_User": this._pays,
      "Status_Admin": this._status,
    };
    if (this._id !== -1) {
      jsonToSend['Id_User'] = this._id;
    }
    return jsonToSend;
  }

  public static fromJSONs(rawsProduct: any[]): Utilisateur[] {
    return rawsProduct.map(Utilisateur.fromJSON);
  }

  public static fromJSON(rawProduct: any): Utilisateur {
    if (rawProduct) {
      const tmpUser = new Utilisateur(rawProduct["Nom_User"], rawProduct["Mdp_User"], rawProduct["NumCompte_User"], rawProduct["Sexe_User"], rawProduct["Login_User"], rawProduct["DateNais_User"], rawProduct["Rue_User"], rawProduct["Num_User"], rawProduct["CodePostal_User"], rawProduct["Ville_User"], rawProduct["Pays_User"], rawProduct["Status_Admin"]);
      tmpUser._id = rawProduct["Id_User"];

      return tmpUser;
    }
    return null;
  }

  public static fromString(util: Utilisateur) {
    const test = JSON.stringify(util);
    return test;
  }


  get id(): number {
    return this._id;
  }

  set id(value: number) {
    this._id = value;
  }

  get mdp(): string {
    return this._mdp;
  }

  set mdp(value: string) {
    this._mdp = value;
  }

  get numCompte(): string {
    return this._compte;
  }

  set numCompte(value: string) {
    this._compte = value;
  }

  get sexe(): string {
    return this._sexe;
  }

  set sexe(value: string) {
    this._sexe = value;
  }

  get login(): string {
    return this._login;
  }

  set login(value: string) {
    this._login = value;
  }

  get dateNais(): string {
    return this._dateNais;
  }

  set dateNais(value: string) {
    this._dateNais = value;
  }

  get rue(): string {
    return this._rue;
  }

  set rue(value: string) {
    this._rue = value;
  }

  get num(): string {
    return this._num;
  }

  set num(value: string) {
    this._num = value;
  }

  get codePostal(): number {
    return this._codePostal;
  }

  set codePostal(value: number) {
    this._codePostal = value;
  }

  get ville(): string {
    return this._ville;
  }

  set ville(value: string) {
    this._ville = value;
  }

  get pays(): string {
    return this._pays;
  }

  set pays(value: string) {
    this._pays = value;
  }


  get status(): boolean {
    return this._status;
  }

  set status(value: boolean) {
    this._status = value;
  }


  get nom(): string {
    return this._nom;
  }

  set nom(value: string) {
    this._nom = value;
  }
}



