import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {Utilisateur} from "../utilisateur";
import {UtilisateurManagerService} from "../utilisateur-manager.service";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  public tmpUser: Utilisateur = new Utilisateur();
  public listUser: Utilisateur[] = [];
  public listUserAjout: Utilisateur[] = [];

  //Logan -- A quoi ça sert ?????
  @Output() public utilisateursChange: EventEmitter<Utilisateur[]> = new EventEmitter();

  constructor(public userService: UtilisateurManagerService) {
  }

  ngOnInit() {

  }

  public returnUtilisateurList() {
    return this.listUser;
  }

  public createUser() {
    this.listUser.push(this.tmpUser);
    this.userService
      .createUtilisateur(this.tmpUser)
      .subscribe(utilisateur => {
        this.tmpUser.id = Utilisateur["Id_User"];
        this.emitUtilisateurs();
        this.emitUtilisateurAdd();
        this.tmpUser = new Utilisateur;
      });

  }

  public emitUtilisateurs() {
    this.utilisateursChange.next(this.listUser);
  }

  public emitUtilisateurAdd() {
    this.utilisateursChange.next(this.listUser);
  }
}



