import {Component, OnInit} from '@angular/core';
import {AuthentificationManagerService} from "../authentification-manager.service";
import {FormControl} from "@angular/forms";
import {ProduitManagerService} from "../produit-manager.service";
import {Product} from "../product";
import {Utilisateur} from "../utilisateur";
import {UtilisateurManagerService} from "../utilisateur-manager.service";

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  public listProduct: Product[] = [];
  public listCateg: string[] = [];
  public list: Product[] = [];
  private _util: Utilisateur;

  constructor(public authentification: AuthentificationManagerService, public productService: ProduitManagerService, public userService: UtilisateurManagerService) {
  }

  ngOnInit() {
    this.productService.getAllProduit().subscribe(listProduct => {
      this.listProduct = Product.fromJSONs(listProduct);
      for (let i = 0; i < this.listProduct.length; i++) {
        if (!this.listCateg.includes(this.listProduct[i].categorie) && i < 6) {
          this.listCateg.push(this.listProduct[i].categorie);
        }
      }
    });

    this.userService.getUtilisateur(this.authentification.login).subscribe(user => {
      this._util = Utilisateur.fromJSON(user);
    });
  }


  get util(): Utilisateur {
    return this._util;
  }

  set util(value: Utilisateur) {
    this._util = value;
  }
}



