import {Component, OnInit} from '@angular/core';
import {Historique} from "../historique";
import {HistoriqueManagerService} from "../historique-manager.service";
import {Utilisateur} from "../utilisateur";

@Component({
  selector: 'app-historique',
  templateUrl: './historique.component.html',
  styleUrls: ['./historique.component.css']
})
export class HistoriqueComponent implements OnInit {

  historique: Historique[] = [];
  private user: string;
  constructor(public historiqueService: HistoriqueManagerService) {
  }

  ngOnInit() {
    console.log(this.getUser());
    this.historiqueService.getHistorique(this.getUser()).subscribe(histo => {
      this.historique = Historique.fromJSONs(histo);
    });
  }

  public getUser() {
    this.user = localStorage.getItem('utilisateur');
    return Utilisateur.fromJSON(this.user).id;
  }
}



