import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {Product} from './product';
import {Utilisateur} from "./utilisateur";

@Injectable()
export class UtilisateurManagerService {

  constructor(public http: HttpClient) {
  }

  public getAllUtilisateur(): Observable<Utilisateur[]> {
    return this.http.get('http://localhost:59583/api/utilisateur');
  }

  public getUtilisateur(login: string): Observable<Utilisateur> {
    console.log(login);
    return this.http.get('http://localhost:59583/api/utilisateur?login=' + login);
  }

  public createUtilisateur(user: Utilisateur): Observable<Utilisateur> {
    return this.http
      .post('http://localhost:59583/api/utilisateur', user.getCleanDataForSending());
  }

  public deleteUtilisateur(id: number): Observable<any> {
    return this.http.delete<string>('http://localhost:59583/api/utilisateur', {
      params: new HttpParams().set('id', id + '')
    });
  }

  public updateUtilisateur(user: Utilisateur): Observable<any> {
    return this.http.put('http://localhost:59583/api/utilisateur', user.getCleanDataForSending());
  }
}





