import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {Product} from "../product";
import {OrderProduct} from "../order-product";
import {ProduitManagerService} from "../produit-manager.service";

@Component({
  selector: 'app-list-product',
  templateUrl: './list-product.component.html',
  styleUrls: ['./list-product.component.css']
})
export class ListProductComponent implements OnInit {

  public tmpProduct: Product = new Product();
  public listProduct: Product[] = [];
  public listProductAjout: Product[] = [];

 @Output() public orderProductChange: EventEmitter<OrderProduct> = new EventEmitter();
 @Output() public productsChange: EventEmitter<Product[]> = new EventEmitter();

  constructor(public productService: ProduitManagerService) {
  }

  ngOnInit() {
    this.productService.getAllProduit().subscribe(listProduct => {
      this.listProduct = Product.fromJSONs(listProduct);
    //  this.emitProducts();
    });
  }

  public returnProductList() {
    return this.listProduct;
  }

  public createProduct() {
    this.listProduct.push(this.tmpProduct);
    this.listProductAjout.push(this.tmpProduct);
    this.productService
      .createProduct(this.tmpProduct)
      .subscribe(product => {
        this.tmpProduct.id = product["Id_Prod"];
    //    this.emitProducts();
        this.tmpProduct = new Product;
      });

  }
/*
  public emitProducts() {
    this.productsChange.next(this.listProduct);
  }
/*
  private emitOrderProduct(product: Product, addQuantity: boolean) {
    console.log("emit");
    this.orderProductChange.next(new OrderProduct(product, addQuantity ? 1 : -1));
  }

  public incrementQuantityOfProduct(product: Product) {
    console.log("+");
    this.emitOrderProduct(product, true);
  }

  public decrementQuantityOfProduct(product: Product) {
    console.log("-");
    this.emitOrderProduct(product, false);
  }
*/

  public deleteProduct(index: number) {
    const DELETE_PRODUIT = () => {
      this.listProduct.splice(index, 1);
      this.listProductAjout = [];
    //  this.emitProducts();
    }
    const DISPLAY_ERROR = (error) => console.error(error);
    this.productService
      .deleteProduct(this.listProduct[index].id)
      .subscribe(DELETE_PRODUIT, DISPLAY_ERROR);
  }

  public updateProduct(prod: Product) {
    this.productService.updateProduct(prod).subscribe();
  }

}



