export class Product {
  private _id: number;
  private _promo: number;
  private _categorie: string;
  private _name: string;
  private _price: number;
  private _stock: number;
  private _urlImage: string;
  private _variationStock = 0;

  constructor(name: string = '', price: number = 0, stock: number = 0, urlImage: string = '', promo: number = 0, categorie: string = '') {
    this._name = name;
    this._price = price;
    this._stock = stock;
    this._urlImage = urlImage;
    this._promo = promo;
    this._categorie = categorie;
  }
  public static fromJSONs(rawsProduct: any[]): Product[] {
    return rawsProduct.map(Product.fromJSON);
  }

  public static fromJSON(rawProduct: any): Product {
    const tmpProduct = new Product(rawProduct["Label_Prod"], rawProduct["Prix_Prod"], rawProduct["Qte_Prod"], rawProduct["UrlImage_Prod"],
      rawProduct["Promo_Prod"], rawProduct["Categorie_Prod"]);
    tmpProduct._id = rawProduct["Id_Prod"];
    return tmpProduct;
  }
  public isOutOfStock(): boolean {
    return this.stock === 0 && this.variationStock === 0;
  }


  public getCleanDataForSending(): any {
    let jsonToSend = {
      "Label_Prod": this._name,
      "Qte_Prod": this._stock,
      "Categorie_Prod": this._categorie,
      "Prix_Prod": this._price,
      "Promo_Prod": this._promo,
      "UrlImage_prod": this._urlImage
    };
    if (this._id !== -1) {
      jsonToSend['Id_Prod'] = this._id;
    }
    if (this.urlImage === '') {
      this._urlImage = 'https://vignette.wikia.nocookie.net/arrow-france/images/e/ea/No-foto.png/revision/latest?cb=20160615100846&path-prefix=fr';
    }
    return jsonToSend;
  }

  public warningNotDone(): string {
    if (this._stock === 0) {
      return 'No stock';
    }
    return '';
  }


  public editRelativeVariationStock(value: number) {
    this.variationStock += value;
  }

  public decrVariation() {
    this._variationStock--;
  }

  public incrVariation() {
    this._variationStock++;
  }

  public updateStockWithVariation() {
    this.stock = this.stockAvailable();
    this._variationStock = 0;
  }

  public stockAvailable(): number {
    return this.stock - this.variationStock;
  }

  public isAvailable(): boolean {
    return this.stockAvailable() > 0;
  }

  public canBeSubtracted(): boolean {
    return this.variationStock > 0;
  }

  public calculerTotal(): number {
    return this.price * this.variationStock;
  }

  get name(): string {
    return this._name;
  }

  set name(value: string) {
    this._name = value;
  }


  get price(): number {
    return this._price;
  }

  set price(value: number) {
    this._price = (value < 0) ? 0 : value;
  }


  get stock(): number {
    return this._stock;
  }

  set stock(value: number) {
    this._stock = (value < 0) ? 0 : value;
  }


  get variationStock(): number {
    return this._variationStock;
  }

  set variationStock(value: number) {
    this._variationStock = value;
  }


  get urlImage(): string {
    return this._urlImage;
  }

  set urlImage(value: string) {
    this._urlImage = value;
  }


  get id(): number {
    return this._id;
  }

  set id(value: number) {
    this._id = value;
  }

  get promo(): number {
    return this._promo;
  }

  set promo(value: number) {
    this._promo = value;
  }

  get categorie(): string {
    return this._categorie;
  }

  set categorie(value: string) {
    this._categorie = value;
  }

}



