import {Component, OnInit} from '@angular/core';
import {Product} from "../product";
import {ProduitManagerService} from "../produit-manager.service";

@Component({
  selector: 'app-accueil',
  templateUrl: './accueil.component.html',
  styleUrls: ['./accueil.component.css']
})
export class AccueilComponent implements OnInit {
  public listProduct: Product[] = [];
  public listCateg: string[] = [];
  public list: Product[] = [];
  constructor(public productService: ProduitManagerService) {
  }

  ngOnInit() {
    this.productService.getAllProduit().subscribe(listProduct => {
      this.listProduct = Product.fromJSONs(listProduct);
      for (let i = 0; i < this.listProduct.length; i++) {
        if (!this.listCateg.includes(this.listProduct[i].categorie) && i < 6) {
          this.listCateg.push(this.listProduct[i].categorie);
          this.list.push(this.listProduct[i]);
        }
      }
    });
  }

}



