import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {ProduitManagerService} from "../produit-manager.service";
import {Product} from "../product";
import {PanierService} from "../panier.service";

@Component({
  selector: 'app-partie-vente',
  templateUrl: './partie-vente.component.html',
  styleUrls: ['./partie-vente.component.css']
})
export class PartieVenteComponent implements OnInit {
  public listCateg: string[] = [];
  public listProduct: Product[] = [];
  public listProductAsk: Product[] = [];

  public tmpProduct: Product = new Product();
  public faute = true;
  public quantite: number[]= [];

  @Output() public productsChange: EventEmitter<Product[]> = new EventEmitter();

  constructor(public productService: ProduitManagerService, public panierService: PanierService) {
  }

  ngOnInit() {
    this.productService.getAllProduit().subscribe(listProduct => {
      this.listProduct = Product.fromJSONs(listProduct);
      this.listProductAsk = Product.fromJSONs(listProduct);
      for (let i = 0; i < this.listProduct.length; i++) {
        if (!this.listCateg.includes(this.listProduct[i].categorie)) {
          this.listCateg.push(this.listProduct[i].categorie);
        }
        this.quantite[i] = 0;
      }
    });
  }

  public rechercherProduitParCat(recherche: string) {
    this.listProductAsk = [];
    for (let i = 0; i < this.listProduct.length; i++) {
      const pos = this.listProduct[i].categorie.toLowerCase().search(recherche.toLowerCase());
      if (pos >= 0) {
        this.listProductAsk.push(this.listProduct[i]);
      }
    }
  }

  public rechercherProduitParNom(recherche: string) {
    this.listProductAsk = [];
    for (let i = 0; i < this.listProduct.length; i++) {
      const pos = this.distLev(this.listProduct[i].name, recherche);
      console.log(pos);
      if (pos <= 2) {
        this.listProductAsk.push(this.listProduct[i]);
      }
    }
  }


  /*PANIER*/

  public ajouterProduitPanier(product: Product, i: number) {
    this.quantite[i] = product.variationStock;
    if (this.quantite[i] > 0) {
      this.quantite[i] = product.variationStock;
      this.panierService.createCommande(product, this.quantite[i]).subscribe();

      if (this.quantite[i].valueOf() <= product.stock) {
        product.stock -= this.quantite[i];
      }
      if (this.quantite[i].valueOf() > product.stock) {
        this.quantite[i] = product.stock;
        product.stock = 0;
      }
      this.productService.updateProduct(product).subscribe();
    }
  }


  public distLev(s: string, t: string) {
    const d = []; //2d matrix

    // Step 1
    const n = s.length;
    const m = t.length;

    if (n === 0) {
      return m;
    }


    if (m === 0) {
      return n;
    }
    //Create an array of arrays in javascript (a descending loop is quicker)
    for (let i = n; i >= 0; i--) {
      d[i] = [];
    }

    // Step 2
    for (let i = n; i >= 0; i--) {
      d[i][0] = i;
    }
    for (let j = m; j >= 0; j--) {
      d[0][j] = j;
    }

    // Step 3
    for (let i = 1; i <= n; i++) {
      let s_i = s.charAt(i - 1);

      // Step 4
      for (let j = 1; j <= m; j++) {

        //Check the jagged ld total so far
        if (i === j && d[i][j] > 4) {
          return n;
        }

        const t_j = t.charAt(j - 1);
        const cost = (s_i == t_j) ? 0 : 1; // Step 5

        //Calculate the minimum
        let mi = d[i - 1][j] + 1;
        const b = d[i][j - 1] + 1;
        const c = d[i - 1][j - 1] + cost;

        if (b < mi) {
          mi = b;
        }
        if (c < mi) {
          mi = c;
        }

        d[i][j] = mi; // Step 6

        //Damerau transposition
        if (i > 1 && j > 1 && s_i === t.charAt(j - 2) && s.charAt(i - 2) === t_j) {
          d[i][j] = Math.min(d[i][j], d[i - 2][j - 2] + cost);
        }
      }
    }
    // Step 7
    return d[n][m];
  }

  public getStock(prod: Product) {

    if (prod.stock === 0) {
      return "INDISPONIBLE";
    }
    return prod.stock;
  }


}



