import {Component, OnInit, Input, OnChanges, SimpleChanges} from '@angular/core';
import {Order} from "../order";

@Component({
  selector: 'app-list-order',
  templateUrl: './list-order.component.html',
  styleUrls: ['./list-order.component.css']
})
export class ListOrderComponent implements OnInit, OnChanges {

  @Input() public orderInjected: Order;
  public orders: Order[] = [];

  constructor() {
  }

  ngOnInit() {
  }


  ngOnChanges(changes: SimpleChanges): void {
    if (this.orderInjected) {
      this.orders.push(this.orderInjected);
    }
  }

  public totalOfAllOrders(): number {
    return this.orders.reduce(
      (tot, currentValue) =>
        tot + currentValue.totalAllOrder(), 0);
  }
}



