import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {Observable} from "rxjs/Observable";
import {Utilisateur} from "./utilisateur";
import {RequestOptions} from "@angular/http";

@Injectable()
export class ConnectionManagerService {
  util: Utilisateur = null;
  constructor(public http: HttpClient) {
  }

  public getConnexion(login: string, password: string): Observable<string> {
    return this.http.get<string>("http://localhost:59583/api/token?username=" + login + "&password=" + password);
  }

  public getUtilisateur(mail: string): Observable<Utilisateur> {
    let headers = new HttpHeaders({'Authorization': 'Bearer ' + localStorage.getItem('TokenStorage')});
    return this.http.get('http://localhost:59583/api/utilisateur?mail=' + mail, {headers: headers});

  }

}



