import {Product} from "./product";
export class OrderProduct {
  private _product: Product;
  private _quantity: number;

  constructor(product: Product, quantity: number = 0) {
    this._product = product;
    this._quantity = quantity;
  }

  get product(): Product {
    return this._product;
  }

  set product(value: Product) {
    this._product = value;
  }

  get quantity(): number {
    return this._quantity;
  }

  set quantity(value: number) {
    this._quantity = (value < 0) ? 0 : value;
  }

  public total(): number {
    return this._product.price * this.quantity;
  }

  public incrQuantity() {
    this.relativeModificationQuantity(1);
  }

  public decrQuantity() {
    this.relativeModificationQuantity(-1);
  }

  public relativeModificationQuantity(quantityToMod: number) {
    this.quantity += quantityToMod;
    this.product.editRelativeVariationStock(quantityToMod);
  }
}

export declare type OrderProducts = OrderProduct[];



