import {Pipe, PipeTransform} from '@angular/core';
import {Utilisateur} from './utilisateur';

@Pipe({
  name: 'UserFilter'
})
export class UserPipe implements PipeTransform {

  private static readonly ALL: number = 0;
  private static readonly Femme: number = 1;
  private static readonly Homme: number = 2;

  transform(value: Utilisateur[], filterWanted: number = 0): Utilisateur[] {

    if (filterWanted === UserPipe.ALL) {
      return value;
    }


    return value.filter((user) => (filterWanted === UserPipe.Homme) ? user.sexe.toLowerCase() === 'homme' : user.sexe.toLowerCase() === 'femme');
  }

}



