import {Component, OnInit} from '@angular/core';
import {AuthentificationManagerService} from "../authentification-manager.service";
import {Utilisateur} from "../utilisateur";
import {UtilisateurManagerService} from "../utilisateur-manager.service";

@Component({
  selector: 'app-user-modif',
  templateUrl: './user-modif.component.html',
  styleUrls: ['./user-modif.component.css']
})
export class UserModifComponent implements OnInit {
  private _utili: Utilisateur = new Utilisateur;

  constructor(public userService: UtilisateurManagerService, public authentification: AuthentificationManagerService) {
    this.userService.getUtilisateur(this.authentification.login).subscribe(user => {
      this._utili = Utilisateur.fromJSON(user);
    });
  }

  ngOnInit() {
    this.userService.getUtilisateur(this.authentification.login).subscribe(user => {
      this._utili = Utilisateur.fromJSON(user);
    });
  }

  public updateUtilisateur(user: Utilisateur) {
    this.userService.updateUtilisateur(user).subscribe();
  }

  public parametre() {
    this.userService.getUtilisateur(this.authentification.login).subscribe(user => {
      this._utili = Utilisateur.fromJSON(user);
    });
  }

  public valid() {
    localStorage.setItem("utilisateur", Utilisateur.fromString(this._utili));
    localStorage.setItem("log", this.utili.login);
    this._utili = new Utilisateur;
    this.parametre();
  }


  get utili(): Utilisateur {
    return this._utili;
  }

  set utili(value: Utilisateur) {
    this._utili = value;
  }
}



