import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {FormsModule} from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';


import {AppComponent} from './app.component';
import {ListProductComponent} from './list-product/list-product.component';
import {CurrentOrderComponent} from './current-order/current-order.component';
import {ListOrderComponent} from './list-order/list-order.component';
import {AdminProductGestionComponent} from './admin-product-gestion/admin-product-gestion.component';
import {ProduitManagerService} from './produit-manager.service';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {ListUtilisateurComponent} from './list-utilisateur/list-utilisateur.component';
import {UtilisateurManagerService} from './utilisateur-manager.service';
import {AccueilComponent} from './accueil/accueil.component';
import {PartieVenteComponent} from './partie-vente/partie-vente.component';
import {RegisterComponent} from './register/register.component';
import {LoginComponent} from './login/login.component';
import {ConnectionManagerService} from './connection-manager.service';
import {PanierComponent} from './panier/panier.component';

import {TokenInterceptor} from './interceptor';
import {PanierService} from './panier.service';
import {UserPipe} from './user.pipe';
import {AuthentificationManagerService} from "./authentification-manager.service";
import {NavbarComponent} from "./navbar/navbar.component";
import {LoginConnectedComponent} from "./login-connected/login-connected.component";
import {UserModifComponent} from './user-modif/user-modif.component';
import {HistoriqueComponent} from './historique/historique.component';
import {HistoriqueManagerService} from "./historique-manager.service";

const routes: Routes = [
  {path: 'administration', component: AdminProductGestionComponent},
  {path: 'gestion-produit', component: ListProductComponent},
  {path: 'gestion-utilisateur', component: ListUtilisateurComponent},
  {path: 'gestion-produit', component: ListProductComponent},
  {path: 'accueil', component: AccueilComponent},
  {path: 'produit-vente', component: PartieVenteComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'panier', component: PanierComponent},
 {path: 'historique', component: HistoriqueComponent}
];

@NgModule({
  declarations: [
    AppComponent,
    ListProductComponent,
    CurrentOrderComponent,
    ListOrderComponent,
    AdminProductGestionComponent,
    ListUtilisateurComponent,
    AdminProductGestionComponent,
    AccueilComponent,
    PartieVenteComponent,
    RegisterComponent,
    LoginComponent,
    LoginConnectedComponent,
    PanierComponent,
    UserPipe,
    NavbarComponent,
    UserModifComponent,
    HistoriqueComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    [RouterModule.forRoot(routes)]
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    },
    ProduitManagerService,
    UtilisateurManagerService,
    ConnectionManagerService,
    PanierService,
    AuthentificationManagerService,
    HistoriqueManagerService
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}




