import {Injectable} from '@angular/core';
import {BehaviorSubject} from "rxjs/BehaviorSubject";
import {Product} from "./product";
import {HttpClient, HttpParams} from "@angular/common/http";
import {Observable} from "rxjs/Observable";
import {Commande} from "./commande";
import {ProduitManagerService} from "./produit-manager.service";
import {Utilisateur} from "./utilisateur";


@Injectable()
export class PanierService {

  private tmpCommande: Commande;
  private tmpProduct: Product;
  private user: string;

  constructor(public http: HttpClient, public productService: ProduitManagerService) {
  }

  public getAllCommande(id_User: number): Observable<Commande[]> {
    return this.http.get('http://localhost:59583/api/commande', {
      params: new HttpParams().set('id', id_User + '')
    });
  }

  public createCommande(product: Product, quantite: number): Observable<Product> {
    this.tmpCommande = new Commande(product.id, quantite , this.getUser(), product.calculerTotal());
  return this.http
.post('http://localhost:59583/api/commande', this.tmpCommande.getCleanDataForSending());

}

public deleteCommande(id: number): Observable<any> {
  let produit = new Product();
this.productService.getProduit(id).subscribe(prod => {
  produit = Product.fromJSON(prod);
  produit.stock += produit.variationStock;
  this.productService.updateProduct(produit).subscribe();
});

return this.http.delete<string>('http://localhost:59583/api/commande', {
  params: new HttpParams().set('id', id + '')
});
}
public deleteCommandeFin(id: number): Observable<any>{
     return this.http.delete<string>('http://localhost:59583/api/commande', {
       params: new HttpParams().set('id_User', id + '')
     });
   }

  public getUser() {
    this.user = localStorage.getItem('utilisateur');
    console.log(Utilisateur.fromJSON(this.user).id+' get');
    return Utilisateur.fromJSON(this.user).id;
  }
}




