import { Injectable } from '@angular/core';
import {HttpClient, HttpParams} from "@angular/common/http";
import {Observable} from "rxjs/Observable";
import {Historique} from "./historique";

@Injectable()
export class HistoriqueManagerService {

  constructor(public http: HttpClient) {
  }


  public getHistorique(id_User: number): Observable<any> {
   return this.http.get('http://localhost:59583/api/historique', {
     params: new HttpParams().set('id', id_User + '')});

  }

  public createHistorique(hist: Historique): Observable<any> {
    return this.http
      .post('http://localhost:59583/api/historique', hist.getCleanDataForSending());
  }


}



