import {Component, OnInit} from '@angular/core';
import {Product} from "../product";
import {PanierService} from "../panier.service";
import {Commande} from "../commande";
import {ProduitManagerService} from "../produit-manager.service";
import {HistoriqueManagerService} from "../historique-manager.service";
import {Historique} from "../historique";
import {Utilisateur} from "../utilisateur";


@Component({
  selector: 'app-panier',
  templateUrl: './panier.component.html',
  styleUrls: ['./panier.component.css']
})
export class PanierComponent implements OnInit {

  panier: Commande[] = [];
  listeProduitPanier: Product[] = [];
  private resume: string;
  private user: string;

  constructor(public panierService: PanierService, public productService: ProduitManagerService, public historiqueService: HistoriqueManagerService) {
  }

  ngOnInit() {

    this.panierService.getAllCommande(this.getUser()).subscribe(products => {
      this.panier = Commande.fromJSONs(products);
      for (let i = 0; i < this.panier.length; i++) {
        this.productService.getProduit(this.panier[i].id_Prod).subscribe(prod => {
          this.listeProduitPanier.push(Product.fromJSON(prod));
        });
      }
    });
  }

  public deleteCommande(index: number, i: number) {
    const DELETE_PRODUIT = () => {
      this.panier.splice(i, 1);
      this.listeProduitPanier.splice(i, 1);
    }
    this.panierService.deleteCommande(index).subscribe(DELETE_PRODUIT);
  }

  public getTotCommande(index: number) {
    return this.panier[index].total;
  }

  public getQuantite(index: number) {
    return this.panier[index].quantite;
  }

  public calculerTotal() {
    let tot = 0;
    for (let i = 0; i < this.panier.length; i++) {
      tot += this.getTotCommande(i);
    }
    return tot;
  }

  public validerCommande() {
    this.resume = ' ';
    for (let i = 0; i < this.listeProduitPanier.length; i++) {
      this.resume += this.listeProduitPanier[i].name + "  " + this.panier[i].quantite + " PU:  " + "€ "
        + this.listeProduitPanier[i].price + "TOTAL : " + this.panier[i].total + '€ \n';

    }
    const tmpHistorique = new Historique(this.getUser(), this.resume, this.calculerTotal());


    const VALIDE_COMMANDE = () => {

      this.panierService.deleteCommandeFin(this.getUser()).subscribe();

      this.listeProduitPanier = [];
      this.panier = [];
    };
    this.historiqueService.createHistorique(tmpHistorique).subscribe(VALIDE_COMMANDE);

    this.resume = ' ';
  }

  public getUser() {
    this.user = localStorage.getItem('utilisateur');
    return Utilisateur.fromJSON(this.user).id;
  }
}



