export class Commande {
  private _id_Commande: number;
  private _id_Prod: number;
  private _quantite: number;
  private _id_User: number;
  private _total: number;

  constructor(id_Prod: number, quantite: number, id_User: number, total: number) {
    this._id_Prod = id_Prod;
    this._id_User = id_User;
    this._quantite = quantite;
    this._total = total;

  }
  public static fromJSONs(rawsProduct: any[]): Commande[] {
    return rawsProduct.map( Commande.fromJSON);
  }

  public static fromJSON(rawProduct: any):  Commande {
    const tmpCommande = new  Commande(rawProduct["Id_Produit"], rawProduct[ "Quantite"], rawProduct["Id_Utilisateur"], rawProduct["Total"]);
    tmpCommande._id_Commande = rawProduct["Id_Commande"];
    console.log(tmpCommande);
    return tmpCommande;
  }

  get idCommande(): number {
    return this._id_Commande;
  }

  set id_Commande(value: number) {
    this._id_Commande = value;
  }
  get id_Prod(): number {
    return this._id_Prod;
  }

  set id_Prod(value: number) {
    this._id_Prod = value;
  }

  get quantite(): number {
    return this._quantite;
  }

  set quantite(value: number) {
    this._quantite = value;
  }

  get id_User(): number {
    return this._id_User;
  }

  set id_User(value: number) {
    this._id_User = value;
  }

  get total(): number {
    return this._total;
  }

  set total(value: number) {
    this._total = value;
  }

  public getCleanDataForSending(): any {
    const jsonToSend = {
      "Id_Commande": this._id_Commande,
      "Id_Produit": this._id_Prod,
      "Quantite": this._quantite,
      "Id_User": this._id_User,
      "Total": this._total

    };
    return jsonToSend;
  }
}



