import {Injectable} from '@angular/core';
import {UtilisateurManagerService} from "./utilisateur-manager.service";
import {Utilisateur} from "./utilisateur";
import {LoginComponent} from "./login/login.component";

@Injectable()
export class AuthentificationManagerService {
  public _user: Utilisateur;

  public _login: string;
  private _token: string;

  constructor(public userService: UtilisateurManagerService) {
    this._user = Utilisateur.fromJSON(localStorage.getItem("utilisateur"));
  }


  public stockUser(login: string) {
    this.userService.getUtilisateur(login).subscribe(user => {
      this._user = Utilisateur.fromJSON(user);
      localStorage.setItem("log", login);
      localStorage.setItem("utilisateur", Utilisateur.fromString(this._user));
      this.connect();
    });

  }

  public connect() {
    if (localStorage.getItem("token") === 'null') {
      this._user = null;
      localStorage.removeItem("token");
      localStorage.removeItem("log");
      localStorage.removeItem("utilisateur");
    }
    //  localStorage.setItem("isconnect", localStorage.getItem("utilisateur") ? "true" : '');
  }

  public deconnect() {
    this._user = null;

    localStorage.removeItem("token");
    localStorage.removeItem("log");
    localStorage.removeItem("utilisateur");
  }


  get user(): Utilisateur {
    return Utilisateur.fromJSON(localStorage.getItem("utilisateur"));
  }

  set user(value: Utilisateur) {
    this._user = value;
  }

  get login(): string {
    return localStorage.getItem("log");
  }

  set login(value: string) {
    this._login = value;
  }


  get token(): string {
    return localStorage.getItem("token");
  }

  set token(value: string) {
    this._token = value;
  }
}



