import {Component, OnInit} from '@angular/core';
import {Utilisateur} from "../utilisateur";
import {UtilisateurManagerService} from "../utilisateur-manager.service";
import {ConnectionManagerService} from "../connection-manager.service";
import {tokenize} from "@angular/compiler/src/ml_parser/lexer";
import {AuthentificationManagerService} from "../authentification-manager.service";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public tmpUser: Utilisateur = new Utilisateur();
  private _user: Utilisateur;

  public test: boolean;
  private _token: string;
  constructor(public userService: UtilisateurManagerService, public connectService: ConnectionManagerService, public authentification: AuthentificationManagerService) {
  }

  ngOnInit() {

  }

  get user(): Utilisateur {
    this.userService.getUtilisateur(this.authentification.login).subscribe(user => {
      this._user = Utilisateur.fromJSON(user);
    });
    return this._user;
  }

  set user(value: Utilisateur) {
    this._user = value;
  }

  public verificationLogin(login: string, password: string) {
    this.connectService.getConnexion(login, password).subscribe(token => {
        console.log(token);
        localStorage.setItem("token", token);
        this.authentification.stockUser(login);
      }
    );

  }

  public deconnect(){
    this._token = '';
    this.authentification.deconnect();
  }


  get token(): string {
    return localStorage.getItem("token");
  }

  set token(value: string) {
    this._token = value;
  }
}



