export class Historique {

  private _id_Historique: number;
  private _id_User: number;
  private _resume: string;
  private _total: number;

  constructor(id_User: number, resume: string, total: number) {
    this._id_User = id_User;
    this._resume =  resume;
    this._total = total;

  }
  public static fromJSONs(rawsProduct: any[]): Historique[] {
    return rawsProduct.map( Historique.fromJSON);
  }

  public static fromJSON(rawProduct: any):  Historique {
    const tmpHistorique = new  Historique(rawProduct["Id_User"], rawProduct[ "Resume"], rawProduct["Total"]);
    tmpHistorique._id_Historique = rawProduct["Id_Historique"];
    console.log(tmpHistorique);
    return tmpHistorique;
  }

  public getCleanDataForSending(): any {
    const jsonToSend = {
      "Id_Historique": this._id_Historique,
      "Id_User": this._id_User,
      "Resume": this._resume,
      "Total": this._total
    };
    return jsonToSend;
  }

  get id_Historique(): number {
    return this._id_Historique;
  }

  set id_Historique(value: number) {
    this._id_Historique = value;
  }

  get id_User(): number {
    return this._id_User;
  }

  set id_User(value: number) {
    this._id_User = value;
  }

  get resume(): string {
    return this._resume;
  }

  set resume(value: string) {
    this._resume = value;
  }

  get total(): number {
    return this._total;
  }

  set total(value: number) {
    this._total = value;
  }
}



